<template>
    <div class="login" :style="{ backgroundImage: 'url('+ backgroundImg +')'}">
        <div class="header">
            <span @click="back" class="back">
                <img src="../../assets/img/back.png" class="releaseIcon">
            </span>
            <span class="usersTitle"></span>
            <span class="back"></span>
        </div>
        <div class="loginMode">
            <span class="modeTab" :class="{ active: modeTab.isActive }" v-for="(modeTab, index) in modeTabs" :key="index" @click="tabLogin(index)">{{ modeTab.mode }}</span>
        </div>
        <div class="container">
            <mt-tab-container v-model="active">
                <mt-tab-container-item id="pwdLogin">
                    <mt-cell>
                        <form class="content">
                            <div class="accountAndpwd">
                                <div class="account">
                                    <label>
                                        <span>手机号</span>
                                    </label>
                                    <input type="tel" v-model="loginForm.account" placeholder="请输入11位手机号码" maxlength="11" autocomplete="tel">
                                    <span class="clear" @click="clearAccount">
                                        <img src="../../assets/img/clear.png">
                                    </span>
                                </div>
                                <div class="pwd">
                                    <span class="pwdLeft">
                                        <label>
                                            <span>密&ensp;码</span>
                                        </label>
                                        <input :type="this.pwdType" v-model="loginForm.password" placeholder="请输入您的密码" autocomplete="new-password">
                                    </span>
                                    <span class="showPwd" @click="showPwd">
                                        <img :src="pwdType === 'password' ? eyeClosed : eyeOpen">
                                    </span>
                                    <span class="clear" @click="clearPwd">
                                        <img src="../../assets/img/clear.png">
                                    </span>
                                </div>
                                <button type="button" @click="login">登录</button>
                                <div class="enroll">
                                    <span @click="findBack">找回密码？</span>
                                    <span @click="$router.push('/home/enroll')">立即注册</span>
                                </div>
                            </div>
                        </form>
                    </mt-cell>
                </mt-tab-container-item>
                <mt-tab-container-item id="phoneLogin">
                    <mt-cell>
                        <form class="content">
                            <div class="accountAndpwd">
                                <div class="account">
                                    <label>手机号</label>
                                    <input type="tel" placeholder="请输入11位手机号码" maxlength="11" v-model="phoneLogin.phoneNum" autocomplete="tel">
                                    <span class="clear" @click="clearPhoneNum">
                                        <img src="../../assets/img/clear.png">
                                    </span>
                                </div>
                                <div class="verificationCode">
                                    <span>
                                        <input type="tel" placeholder="请输入您的验证码" maxlength="6" v-model="phoneLogin.verificationCode">
                                    </span>
                                    <span style="border: none;">
                                        <button type="button" id="getCode" :class="{ disabled: !canClick }" @click="getCode">{{ content }}</button>
                                    </span>
                                </div>
                                <button type="button" @click="handlePhoneLogin">登录</button>
                                <div class="enroll">
                                    <span>没有账号？</span>
                                    <span @click="$router.push('/home/enroll')">立即注册</span>
                                </div>
                            </div>
                        </form>
                    </mt-cell>
                </mt-tab-container-item>
            </mt-tab-container>
        </div>
        <div class="otherWay">
            <div class="waysHead">
                <span class="waysTitle">其他方式登录</span>
            </div>
            <div class="otherWays">
                <span>
                    <img src="../../assets/img/loginQq.png">
                </span>
                <span>
                    <img src="../../assets/img/loginWechat.png">
                </span>
                <span>
                    <img src="../../assets/img/loginWeibo.png">
                </span>
            </div>
        </div>
    </div>
</template>

<script>
import { login, getCode, checkAccount, codeLogin } from '../../api/getdata'
// import { setCookie, getCookie } from '../../../static/js/cookieUtil'
import { Toast } from 'mint-ui'
export default {
  name: 'login',
  data () {
    return {
      backgroundImg: require('../../assets/img/login.png'),
      eyeClosed: require('../../assets/img/eyesClose.png'),
      eyeOpen: require('../../assets/img/eyesOpen.png'),
      pwdType: 'password',
      content: '获取验证码',
      totalTime: 60,
      canClick: true,
      active: 'pwdLogin',
      modeTabs: [{
        mode: '账号密码登录',
        isActive: true,
        tab: 'pwdLogin'
      }, {
        mode: '短信验证码登录',
        isActive: false,
        tab: 'phoneLogin'
      }],
      loginForm: {
        account: '',
        password: ''
      },
      phoneLogin: {
        phoneNum: '',
        verificationCode: ''
      },
      isLogin: ''
    }
  },
  mounted () {
    // this.loginForm.account = getCookie('account')
    this.loginForm.account = localStorage.getItem('account')
    // this.loginForm.password = getCookie('password')
    this.loginForm.password = localStorage.getItem('password')
    // this.isLogin = getCookie('userState')
    this.isLogin = localStorage.getItem('userState')
    // this.phoneLogin.phoneNum = getCookie('phoneNum')
    this.phoneLogin.phoneNum = localStorage.getItem('phoneNum')
    // console.log(this.isLogin)
  },
  methods: {
    back () {
      this.$router.push('/home/index')
    },
    findBack () {
      this.$router.push('/home/mine/changePwd')
    },
    tabLogin (index) {
      for (var i = 0; i < this.modeTabs.length; i++) {
        this.modeTabs[i].isActive = false
        this.modeTabs[index].isActive = true
        this.active = this.modeTabs[index].tab
      }
    },
    // 手机号密码登录
    login () {
      // console.log(this.loginForm)
      if (this.loginForm.account && this.loginForm.password) {
        let loginData = login({fMobile: this.loginForm.account, fPassword: this.loginForm.password})
        loginData.then(res => {
          console.log(res)
          // console.log(res.data)
          if (res.status === '-1') {
            Toast({
              message: '输入的账号密码不正确',
              duration: 2000
            })
            // 是否清空已输入账号密码
            // this.loginForm.account = ''
            // this.loginForm.password = ''
          } else if (res.status === '0') {
            localStorage.setItem('account', this.loginForm.account)
            localStorage.setItem('password', this.loginForm.password)
            localStorage.setItem('userState', 'isLogin')
            localStorage.setItem('userType', res.data.fUserType)
            localStorage.setItem('userId', res.data.fUserId)
            // setCookie('account', this.loginForm.account)
            // setCookie('password', this.loginForm.password)
            // setCookie('userState', 'isLogin')
            // console.log(res.data.data.fUserType)
            // setCookie('userType', res.data.fUserType)
            // setCookie('userId', res.data.fUserId)
            Toast({
              message: '登录成功',
              duration: 2000
            })
            // 延迟登录
            setTimeout(() => {
              this.$router.push('/home/index')
            }, 1000)
          }
        })
      } else {
        Toast({
          message: '账号与密码不能为空'
        })
      }
    },
    // 手机号验证码登录
    handlePhoneLogin () {
      if (this.phoneLogin.phoneNum && this.phoneLogin.verificationCode) {
        let data = codeLogin({fMobile: this.phoneLogin.phoneNum, code: this.phoneLogin.verificationCode})
        data.then(res => {
          console.log(res)
          if (res.status === '-1') {
            Toast({
              message: '登录失败',
              duration: 2000
            })
          } else if (res.result === 'error2') {
            Toast({
              message: '验证码以过期，请重新获取',
              duration: 2000
            })
            this.canClick = true
          } else if (res.status === '0') {
            Toast({
              message: '验证成功，正在登录',
              duration: 2000
            })
            localStorage.setItem('userState', 'isLogin')
            localStorage.setItem('userType', res.data.fUserType)
            localStorage.setItem('userId', res.data.fUserId)
            localStorage.setItem('phoneNum', this.phoneLogin.phoneNum)
            // setCookie('userState', 'isLogin')
            // setCookie('userType', res.data.fUserType)
            // setCookie('userId', res.data.fUserId)
            // setCookie('phoneNum', this.phoneLogin.phoneNum)
            setTimeout(() => {
              this.$router.push('/home/index')
            }, 1000)
          }
        })
      }
    },
    showPwd () {
      this.pwdType = this.pwdType === 'password' ? 'text' : 'password'
    },
    // 获取验证码
    getCode () {
      let getcode = getCode({fMobile: this.phoneLogin.phoneNum})
      let checkMobile = checkAccount({fMobile: this.phoneLogin.phoneNum})
      checkMobile.then(res => {
        console.log(res)
        if (res.status === '0') {
          Toast({
            message: '该手机号尚未注册',
            duration: 1500
          })
        } else if (res.status === '-1') {
          getcode.then(res => {
            Toast({
              message: '验证码已发送',
              duration: 1500
            })
          })
            .catch(err => {
              console.log(err)
            })
          if (!this.canClick) {
            return
          }
          this.canClick = false
          this.content = '获取验证码(' + this.totalTime + 's)'
          let clock = window.setInterval(() => {
            this.totalTime--
            this.content = '获取验证码(' + this.totalTime + 's)'
            if (this.totalTime < 0) {
              window.clearInterval(clock)
              this.content = '重新获取验证码'
              this.totalTime = 60
              this.canClick = true
            }
          }, 1000)
        }
      })
    },
    clearAccount () {
      this.loginForm.account = ''
      // setCookie('account', '', -1)
      localStorage.removeItem('account')
      this.loginForm.password = ''
      // setCookie('password', '', -1)
      localStorage.removeItem('password')
    },
    clearPwd () {
      this.loginForm.password = ''
      // setCookie('password', '', -1)
      localStorage.removeItem('password')
    },
    clearPhoneNum () {
      this.phoneLogin.phoneNum = ''
      // setCookie('phoneNum', '', -1)
      localStorage.removeItem('phoneNum')
    }
  }
}
</script>

<style scoped>
  * {
    margin: 0;
    padding: 0;
    font-size: 0.426667rem;
  }

  .header {
    height: 1.5rem;
    display: flex;
    justify-content: space-between;
    position: relative;
    font-size: 0.48rem;
  }

  .header span {
    display: flex;
    justify-content: center;
    padding: 0.5rem;
    line-height: 0.5rem;
  }

  .back {
    height: 0.5rem;
    width: 0.266667rem;
  }

  img {
    width: 100%;
  }

  .login {
    width: 100%;
    height: auto;
    background-size: 100% 100%;
    background-repeat: no-repeat;
    background-position: center;
  }

  .loginMode {
    margin-top: 4.2rem;
  }

  .content {
    width: 100%;
  }

  .modeTab {
    text-align: center;
    color: #666666;
    padding: 0 0.5rem;
  }

  .modeTab.active {
    color: #333333;
    font-size: 0.48rem;
  }

  .account,
  .pwd {
    display: flex;
    margin: 0.6667rem auto;
    border-bottom: 1px solid #ffd200;
    align-items: center;
  }

  .container {
    width: calc(100% - 1.22667rem);
    margin: 0 auto;
  }

  .accountAndpwd {
    width: 100%;
  }

  .accountAndpwd label{
    width: 2.2rem;
    justify-content: center;
    display: flex;
    align-items: center;
    color: #ffd200;
  }

  .accountAndpwd label span {
    vertical-align: middle;
    font-size: 0.42667rem;
  }

  .account input,
  .pwd input {
    background: transparent;
    border: none;
    outline: none;
    height: 1.33333rem;
    flex-grow: 1;
    font-size: 0.42667rem;
  }

  .accountAndpwd button {
    background: #ffd200;
    outline: none;
    border: none;
    color: #fff;
    font-size: 0.48rem;
  }

  .accountAndpwd button,
  .account,
  .pwd {
    width: 100%;
    height: 1.33333rem;
  }

  .accountAndpwd button {
    border-radius: 0.15rem;
  }

  .pwd {
    display: flex;
    justify-content: space-between;
  }

  .pwdLeft {
    display: flex;
    flex-grow: 1;
  }

  .showPwd {
    display: flex;
    align-items: center;
    width: 0.6rem;
    margin-right: 0.3rem;
    margin-top: 0.1rem;
  }

  .showPwd img {
    width: 100%;
  }

  .enroll {
    width: calc(100% - 0.5rem);
    margin: 0 auto;
    margin-top: 0.346667rem;
    display: flex;
    justify-content: space-between;
  }

  .enroll span {
    font-size: 0.373333rem;
    color: #333333;
  }

  .otherWay {
    width: 7.5rem;
    margin: 0 auto;
    margin-top: 1rem;
  }

  .waysTitle {
    display: block;
    position: relative;
    color: #333333;
    font-size: 0.373333rem;
  }

  .waysTitle::before,
  .waysTitle::after {
    content: '';
    position: absolute;
    top: 48%;
    width: 2.1rem;
    height: 1px;
    background: #333333;
  }

  .waysTitle::before {
    left: 70%;
  }

  .waysTitle::after {
    right: 70%;
  }

  .otherWays {
    display: flex;
    justify-content: space-between;
    margin-top: 1rem;
    padding: 0 0.2rem;
  }

  .otherWays span {
    display: block;
    width: 1.173333rem;
    height: 1.173333rem;
    border: 1px solid #666666;
    border-radius: 50%;
  }

  .otherWays span img {
    width: 100%;
    height: 100%;
  }

  .verificationCode {
    display: flex;
    justify-content: space-between;
    margin-bottom: 0.6667rem;
  }

  .verificationCode input {
    height: 1.3333rem;
    background: transparent;
    border: none;
    outline: none;
    padding: 0 0.6rem 0 0.4rem;
    color: #333333;
  }

  .verificationCode span {
    display: block;
    width: 4.266667rem;
    border-bottom: 1px solid #ffd200;
  }

  .disabled {
    background: #ddd!important;
  }

  .clear {
    display: flex;
    width: 0.56rem;
    height: 0.56rem;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;
  }

  .showPwd {
    flex-shrink: 0;
  }

  input::placeholder {
    color: #c0c0c0;
    font-size: 0.4rem;
  }
</style>



